<template>
  <div class="orders">
    <MBX label1="" label2=""></MBX>
    <el-card>
      <!--  -->
      <el-input
        style="width: 400px"
        placeholder=""
        v-model="current.query"
        class="input-with-select"
        clearable
        @clear="clear"
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="search"
        ></el-button>
      </el-input>

      <!--  -->
      <template>
        <el-table
          :data="tableData"
          class="mt"
          border
          stripe
          style="width: 100%"
        >
          <el-table-column type="index" label="#" width="50"> </el-table-column>
          <el-table-column prop="order_number" label="">
          </el-table-column>
          <el-table-column prop="order_price" label="">
          </el-table-column>
          <el-table-column prop="pay_status" label="">
            <template slot-scope="scope">
              <el-button style="padding: 3px" size="mini" type="danger">{{
                scope.row.pay_status == 0 ? '' : ''
              }}</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="is_send" label="">
            <template slot-scope="scope">
              {{ scope.row.is_send }}
            </template>
          </el-table-column>
          <el-table-column prop="update_time" label="">
            <template slot-scope="scope">
              {{
                new Date(scope.row.update_time * 1000)
                  .toLocaleString()
                  .replace(/\//g, '-')
              }}
            </template>
          </el-table-column>
          <el-table-column label="" width="150px">
            <template slot-scope="scope">
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top-start"
              >
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="updates(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="dark"
                content=""
                placement="top-start"
              >
                <el-button
                  size="mini"
                  type="success"
                  icon="el-icon-location-information"
                  @click="set(scope.row)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!--  -->
      <div class="block">
        <el-pagination
          class="mt"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="current.pagenum"
          :page-sizes="[5, 8, 10]"
          :page-size="current.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        :before-close="handleClose"
      >
        <el-form
          :model="form"
          :rules="rules"
          ref="form"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="/" prop="one">
            <el-cascader
              :options="cityOptions"
              v-model="form.one"
              @change="changeProvince"
            >
            </el-cascader>
          </el-form-item>
          <el-form-item label="" prop="two">
            <el-input v-model="form.two"></el-input>
          </el-form-item>
        </el-form>
        <div v-show="title == 2">
          <li> : {{ userBox.order_number }}</li>
          <li class="mt"> : {{ userBox.role_name }}</li>
          <div class="mt">
             :
            <el-cascader
              v-model="value"
              :options="options"
              @change="handleChange"
              :props="defaultParams"
            ></el-cascader>
          </div>
        </div>

        <span slot="footer" class="dialog-footer">
          <el-button @click="out"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </span>
      </el-dialog>

      <!--   -->

      <el-dialog :visible.sync="flag" title="">
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in City"
            :key="index"
            :icon="activity.location"
            :type="activity.type"
            :color="activity.color"
            :size="activity.size"
            :timestamp="activity.timestamp"
          >
            {{ activity.context }}
          </el-timeline-item>
        </el-timeline>
        <span slot="footer" class="dialog-footer">
          <el-button @click="flag = false"> </el-button>
          <el-button type="primary" @click="flag = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import {
  GetList, //
  UpArr, //
  Search, //
  WuLiu //
} from '../../api/orders'
import City from '../../assets/js/locat'
import cityOptions from '../../assets/js/city_data2017_element'
import MBX from '@/components/MBX.vue'
export default {
  data() {
    return {
      City: City[0].data,
      flag: false,
      title: 0, //
      cityOptions: cityOptions,
      city: [],
      form: {
        //
        one: [],
        two: ''
      },
      rules: {
        two: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 15, message: '', trigger: 'blur' }
        ]
      },
      tableData: [],
      tableDataS: [],
      current: {
        query: '', //
        pagenum: 1, //
        pagesize: 5 //
      },
      total: 10, //
      dialogVisible: false, //
      id: 0, //ID
      //
      userBox: {},
      value: [], //
      options: [
        //
        {
          value: 'zhinan',
          label: ''
        }
      ],
      //
      defaultParams: {
        label: 'roleName',
        value: 'id',
        children: ''
      }
    }
  },
  methods: {
    changeProvince() {},
    //
    async send() {
      const List = await GetList(this.current)
      console.log(List)
      this.current.pagenum = List.pagenum * 1
      this.total = List.total
      this.tableData = List.goods
      this.tableDataS = this.tableData
    },
    // 
    async set(row) {
      console.log(row)
      this.flag = true
    },
    //
    out() {
      this.$refs.form.resetFields()
      this.dialogVisible = false
    },
    // 
    async submitForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          this.send()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    handleSizeChange(val) {
      this.current.pagesize = val
      this.send()
      console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      this.current.pagenum = val
      this.send()
      console.log(`: ${val}`)
    },
    handleClose(done) {
      this.$confirm('')
        .then((_) => {
          done()
        })
        .catch((_) => {})
    },
    //
    updates(ele) {
      this.$refs.form ? this.$refs.form.resetFields() : ''
      console.log(ele)
      this.id = ele.id
      this.dialogVisible = true
      this.title = 1
      this.form = JSON.parse(JSON.stringify(ele))
      console.log(this.form)
    },

    //
    search() {
      //   if(this.current.query.length==0){
      //       alert("")
      //   };
      console.log(parseFloat(this.current.query).toString() !== 'NaN')
      if (parseFloat(this.current.query).toString() !== 'NaN') {
        // const data = await Search({ id: this.current.query });
        Search({ id: this.current.query })
          .then((res) => {
            this.tableData = [res]
          })
          .catch((res) => {
            this.tableData = []
          })
      } else {
        console.log(this.tableDataS)
        console.log(this.current.query)
        this.tableData = this.tableDataS.filter((item) => {
          return item.order_number.includes(this.current.query.trim())
        })
      }
    },
    //
    clear() {
      this.send()
    },
    //
    async change(e) {
      console.log(e)
      const data = await State({ id: e.id, type: e.mg_state })
      this.send()
    },
    handleChange(value) {
      this.value = value
    }
  },
  components: { MBX },
  computed: {},
  watch: {},
  created() {
    this.send()
    this.City[0].location = 'el-icon-sunset'
  }
}
</script>
<style lang="scss" scoped>
.orders {
  width: 100%;
  min-width: 1140px;
  height: calc(100% - 30px);
  ::v-deep .has-gutter th {
    font-weight: 700;
  }
}
::v-deep .el-scrollbar__wrap {
  overflow-x: hidden !important;
}
::v-deep .el-icon-sunset:before {
  color: red;
  font-size: 25px;
  background: pink;
}
</style>
