import api from "./request";
export function GetList(params) {
  // 
  return api({
    url: 'orders',
    method: 'GET',
    params
  })
}
export function UpArr(data) {
  // 
  return api({
    url: 'orders/' + data.id,
    method: 'PUT',
    data
  })
}

export function Search(data) {
  // 
  return api({
    url: 'orders/' + data.id,
    method: 'GET'
  })
}
export function WuLiu(data) {
  // 
  return api({
    url: 'kuaidi/' + 1106975712662,
    method: 'GET'
  })
}